import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { IRegistration } from './userRegister';

import { RegisterService } from '../../../services/register.service';
import { AlertService } from '../../../services/alert.service';

import { FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';


@Component({
  selector: 'app-register',
  templateUrl: './register.page.html',
  styleUrls: ['./register.page.scss'],
})
export class RegisterPage implements OnInit {
  private subscription_register: Subscription = new Subscription();
  cpassword: string;

  register: IRegistration = {
    name: null,
    accountType: null,
    isStudent: null,
    emailAddress: null,
    address: null,
    password: null,
  };
accountType: any;
isStudent: any;

  constructor(
    private router: Router,
    private alertService: AlertService,
    private registerService: RegisterService
  ) { }

  ngOnInit() {

  }
  onSubmit(data: NgForm){
    console.log(data.form);
    this.subscription_register.add(
      this.registerService.register(
        data.form.value.emailAddress,
        data.form.value.name,
        this.accountType,
        data.form.value.address,
        data.form.value.password,
        this.isStudent
        )
      .subscribe(
        result => {
          console.log(result);
          if(result.status == 'Success'){
            this.alertService.alertWithTimer('success', 'Registration Successful', '');
            this.router.navigate(['login']);
          }
          else{
            this.alertService.simpleAlert('error', ': (', 'Something went wrong, please try again');
          }
        },
        error => {
          this.alertService.simpleAlert('error', ': (', 'Something went wrong, please try again');
        }
      )
    );
    // this.router.navigate(['login']);
    // return 0;

  }

}
